
.mcs{
	position: relative;

	.container{
		padding: 0 20px 180px;
	}

	.team-mc{
		margin-top: -50px;
	}

	&__decor-fish{
		position: absolute;
		top: 200px;
		left: 50%;
		margin: 0 0 0 -503px;
		width: 322px;
		height: 200px;
		background: url(../img/decor_fish.png) center center no-repeat;
		background-size: 100%;
	}

	&__decor-sub{
		position: absolute;
		bottom: 80px;
		left: 50%;
		margin: 0 0 0 118px;
		width: 543px;
		height: 350px;
		background: url(../img/decor_submarine.png) center center no-repeat;
		background-size: 100%;
	}

	&:before{
		position: absolute;
		content: '';
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		background: url(../img/bg-dots.png) center top;
		z-index: -1;

	}

	&:after{
		position: absolute;
		content: '';
		left: 0;
		top: 0;
		right: 0;
		height: 600px;
		background: url(../img/bg-dots.png) center top;
		z-index: -1;
		background: linear-gradient(to bottom, rgba(255,255,255,1) 0%,rgba(255,255,255,0) 100%);
	}

	@include lg{
		.team-mc{
			margin-top: 40px;
		}

		.container{
			padding: 0 20px 50px;
		}
	}

	@include xs{


		&__decor-fish{
			top: 54px;
			margin: 0px 0 0 -18px;
			width: 170px;
			height: 108px;
		}

		&__decor-sub{
			top: 570px;
			bottom: auto;
			margin: 0 0 0 -28px;
			width: 240px;
			height: 154px;

		}
	}
}

.team-mc{


	&__item{
		position: relative;
		width: 370px;
		height: 370px;
		margin: 0 0 20px auto;
		text-align: right;

		&:nth-child(2n){
			margin: 0 auto 20px 0;
			text-align: left;

			.team-mc__content{
				left: 345px;
				right: auto;
			}

			.team-mc__desc{

				&:after{
					right: auto;
					left: -21px;
					width: 0;
					height: 0;
					border-style: solid;
					border-width: 0 21px 21px 0;
					border-color: transparent #24fc89 transparent transparent;
				}

			}
		}
	}

	&__img-wrap{
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		width: 100%;
		height: 100%;

		img{
			display: block;
			width: 100%;
			height: auto;
		}
	}

	&__head{
		padding: 0 48px;
	}

	&__content{
		position: relative;
		left: auto;
		right: 345px;
		bottom: 0;
		padding-top: 110px;
		width: 370px;
		z-index: 1;
	}

	&__desc{
		background: $clr-green;
		padding: 48px 48px 14px;
		position: relative;
		text-align: left;

		&:after{
			content: '';
			position: absolute;
			right: -21px;
			top: 31px;
			width: 0;
			height: 0;
			border-style: solid;
			border-width: 21px 21px 0 0;
			border-color: #24fc89 transparent transparent transparent;
		}

	}

	&__bottom{
		background: $clr-green;
		padding: 0 46px 40px;
		display: flex;
		justify-content: space-between;
	}

	&__name{
		font-size: 35px;
		font-weight: 700;
		line-height: 1.2;
		padding: 0;
		margin: 0 0 -3px;
	}

	&__company{
		font-size: 20px;
		line-height: 1.38;
		padding: 0;
		margin: 0px 0 24px;
	}

	&__info{
		font-size: 14px;
		line-height: 1.5;
		padding: 0;
		margin: 0;
	}

	&__bottom{
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin: 0;
	}

	&__tag{
		margin: 0;
		font-size: 14px;
		font-weight: 600;
	}

	.scls{
		margin: 0;

		&__item{
			margin: 0;
		}

		&__link{
			&:hover{

				.scls__img{
					transform: scale(1.4);
					fill: $clr-white;
				}
			}
		}

		&__img{
			transform: scale(1.4);
			fill: $clr-black;
		}
	}

	@include lg{

		&__item{
			height: auto;
			margin-bottom: 40px;
		}

		&__content{

		}
	}

	@include sm{

		&__item{
			width: 100%;

			&:nth-child(2n){
				margin: 0 auto 20px 0;
				text-align: left;


				.team-mc__content{
					margin: 0 auto 0 220px;
					left: 0;
				}

				.team-mc__img-wrap{
					margin: 0 auto 0 0;
				}
			}
		}

		&__img-wrap{
			max-width: 250px;
			margin: 0 0 0 auto;
		}

		&__content{
			position: relative;
			margin: 0 220px 0 auto;
			right: 0;
			padding-top: 50px;
			max-width: 370px;
			width: auto;
		}

		&__desc{
			padding: 24px 24px 0;
		}

		&__bottom{
			padding: 0 24px 14px;
		}
	}

	@include xs{
		margin-left: -20px;
		margin-right: -20px;

		&__item{
			width: 100%;
			margin-bottom: 136px;
			padding-top: 1px;

			&:nth-child(2n){
				margin: 0 auto 20px 0;
				text-align: left;


				.team-mc__content{
					margin: 0 auto 0 70px;
				}

				.team-mc__img-wrap{
					margin: 0 auto 0 0;
				}
			}
		}

		&__content{
			margin: 0 70px 0 auto;
			padding-top: 192px;
		}

		&__desc{
			padding-top: 12px;

			&:after{
				top: -48px;
			}
		}

		&__head{
			padding: 24px 24px 0;
			background: $clr-green;
			text-align: left;
		}

		&__name{
			font-size: 20px;
			margin-bottom: 2px;
		}

		&__company{
			font-size: 14px;
			margin: 0;
		}

		&__info{
			font-size: 12px;
		}
	}

}