/* Buttons
-----------------------------------------------------------------------------*/

.basic-btn{
	display: inline-flex;
	font-size: 13px;
	height: 62px;
	padding: 0 0;
	background: $clr-black;
	color: $clr-white;
	align-items: center;
	text-align: center;
	text-decoration: none;
	font-weight: 500;
	overflow: hidden;
	outline: none;
	border: 2px solid $clr-black;
	cursor: pointer;
	min-width: 60px;

	&__inner{
		position: relative;
		width: 100%;
		height: 100%;
		display: flex;
		align-items: center;
		padding: 0 20px 0 80px;
		transform: translateX(-60px);
		transition: all ease .2s;
	}

	&__arr-l,
	&__arr-r{
		position: absolute;
		left: 0;
		top: 0;
		bottom: 0;
		display: flex;
		width: 60px;
		height: 100%;
		align-items: center;
		justify-content: center;
	}

	&__arr-r{
		left: auto;
		right: -60px;
	}

	&__arrow{
		position: relative;
		width: 21px;
		height: 10px;
		margin: 0;
		fill: $clr-white;
		position: absolute;
		transform: translateX(0);
		transition: all .2s ease;
		opacity: 1;
	}

	&:hover{

		.basic-btn__inner{
			transform: translateX(0);
		}
	}

	&.btn-green{
		background: $clr-green;
		border-color: $clr-green;
		color: $clr-black;

		.basic-btn__arrow{
			fill: $clr-black;
		}
	}

	&.btn-black{
		background: $clr-black;
		color: $clr-green;
		border: 2px solid $clr-green;

		.basic-btn__arrow{
			fill: $clr-green;
		}
	}

	&.marged{
		margin-top: 20px;
	}
}