html, body { width: 100%; height: 100%; }

html { font-family: "GothamPro", Arial, Helvetica, sans-serif; }

body { width: 100%; background: #fff; color: #171717; font-family: "GothamPro", Arial, Helvetica, sans-serif; font-size: 16px; line-height: 1.36; }

a { color: #18db73; transition: all linear .2s; }

a:hover { text-decoration: none; }

.clearfix:after { clear: both; display: block; visibility: hidden; height: 0; content: "."; font-size: 0; }

.clearfix { zoom: 1; }

.ir { font: 0/0 a; text-shadow: none; color: transparent; }

.wrapper { position: relative; overflow: hidden; min-height: 100%; min-width: 300px; }

.container { position: relative; max-width: 1220px; margin: 0 auto; padding: 130px 20px; }

@media only screen and (max-width: 768px) { .container { padding: 65px 20px; } }

.bg-black { background: #171717; color: #fff; }

.d-block { display: block; }

.d-flex { display: flex; }

.d-none { display: none; }

.my-0 { margin-top: 0; margin-bottom: 0; }

.mt-0 { margin-top: 0; }

.mb-0 { margin-bottom: 0; }

.py-0 { padding-top: 0; padding-bottom: 0; }

.pt-0 { padding-top: 0; }

.pb-0 { padding-bottom: 0; }

b, h1, h2, h3, h4, h5, h6 { font-family: "GothamPro", Arial, Helvetica, sans-serif; }

/* Fonts
-----------------------------------------------------------------------------*/
@font-face { font-family: "GothamPro"; src: url(../fonts/GothamPro.woff2) format("woff2"), url(../fonts/GothamPro.woff) format("woff"); font-style: normal; font-weight: 400; }

@font-face { font-family: "GothamPro"; src: url(../fonts/GothamProMedium.woff2) format("woff2"), url(../fonts/GothamProMedium.woff) format("woff"); font-style: normal; font-weight: 500; }

@font-face { font-family: "GothamPro"; src: url(../fonts/GothamProBold.woff2) format("woff2"), url(../fonts/GothamProBold.woff) format("woff"); font-style: normal; font-weight: 700; }

@font-face { font-family: "RumbleBrave"; src: url(../fonts/RumbleBrave.woff2) format("woff2"), url(../fonts/RumbleBrave.woff) format("woff"); font-style: normal; font-weight: 400; }

/* Icons
-----------------------------------------------------------------------------*/
/* Slider */
.slick-slider { position: relative; display: block; box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }

.slick-list:focus { outline: none; }

.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-track, .slick-slider .slick-list { -webkit-transform: translate3d(0, 0, 0); -moz-transform: translate3d(0, 0, 0); -ms-transform: translate3d(0, 0, 0); -o-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; margin-left: auto; margin-right: auto; }

.slick-track:before, .slick-track:after { content: ""; display: table; }

.slick-track:after { clear: both; }

.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; height: 100%; min-height: 1px; display: none; }

[dir="rtl"] .slick-slide { float: right; }

.slick-slide img { display: block; }

.slick-slide.slick-loading img { display: none; }

.slick-slide.dragging img { pointer-events: none; }

.slick-initialized .slick-slide { display: block; }

.slick-loading .slick-slide { visibility: hidden; }

.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }

.slick-arrow.slick-hidden { display: none; }

/* Forms
-----------------------------------------------------------------------------*/
/* Buttons
-----------------------------------------------------------------------------*/
.basic-btn { display: inline-flex; font-size: 13px; height: 62px; padding: 0 0; background: #171717; color: #fff; align-items: center; text-align: center; text-decoration: none; font-weight: 500; overflow: hidden; outline: none; border: 2px solid #171717; cursor: pointer; min-width: 60px; }

.basic-btn__inner { position: relative; width: 100%; height: 100%; display: flex; align-items: center; padding: 0 20px 0 80px; transform: translateX(-60px); transition: all ease .2s; }

.basic-btn__arr-l, .basic-btn__arr-r { position: absolute; left: 0; top: 0; bottom: 0; display: flex; width: 60px; height: 100%; align-items: center; justify-content: center; }

.basic-btn__arr-r { left: auto; right: -60px; }

.basic-btn__arrow { position: relative; width: 21px; height: 10px; margin: 0; fill: #fff; position: absolute; transform: translateX(0); transition: all .2s ease; opacity: 1; }

.basic-btn:hover .basic-btn__inner { transform: translateX(0); }

.basic-btn.btn-green { background: #24fc89; border-color: #24fc89; color: #171717; }

.basic-btn.btn-green .basic-btn__arrow { fill: #171717; }

.basic-btn.btn-black { background: #171717; color: #24fc89; border: 2px solid #24fc89; }

.basic-btn.btn-black .basic-btn__arrow { fill: #24fc89; }

.basic-btn.marged { margin-top: 20px; }

/* Header
-----------------------------------------------------------------------------*/
.header { position: fixed; left: 0; top: 0; right: 0; transition: all .3s ease; z-index: 10; }

.header .container { display: flex; justify-content: space-between; padding: 20px 20px; flex: 1; }

.header.sticky { background: #24fc89; }

.header.sticky .logo-mini { opacity: 1; transform: rotate(0) scale(1); }

.header.sticky .scls__link { fill: #24fc89; }

.header.sticky .scls__link:hover .scls__img { fill: #fff; }

.header__logo { margin: 0 30px 0 0; }

.header__nav { flex: 1 0 auto; }

.header__scls { margin: 0 0 0 14px; }

@media only screen and (max-width: 890px) { .header { transition: all .3s ease; height: 62px; overflow: hidden; }
  .header.sticky { background: #24fc89; }
  .header.sticky .logo-mini { opacity: 1; }
  .header .container { flex-direction: column; height: 100%; }
  .header .header__logo { order: 1; margin: -10px 0 0; }
  .header .header__nav { order: 2; margin: auto 0; display: flex; }
  .header .header__nav .nav { margin: auto; }
  .header .header__scls { order: 3; margin: 0 0 100px; }
  .header .logo-mini { position: relative; display: block; margin: 0 auto; padding: 0; transform: none; left: 0; top: 0; opacity: 0; }
  .header .nav { flex-direction: column; max-width: 300px; margin: 0 auto; }
  .header .scls { margin: 0 auto; justify-content: center; }
  .header .scls__item { text-align: center; margin: 0; } }

@media only screen and (max-width: 890px) { .header.open { visibility: visible; opacity: 1; background: #fff; height: 100vh; } }

.logo-mini { position: relative; margin: 0; height: 44px; width: 56px; opacity: 0; transform: rotate(120deg) scale(0.5); transition: all 0.5s; }

.logo-mini__link { display: block; height: 100%; background: url(../img/logo_mini.svg) no-repeat; transition: none; background-size: 100%; }

.nav { font-size: 14px; display: flex; padding: 0 10px; margin: 0 auto; list-style: none; font-weight: 700; flex: 1 1 auto; }

.nav__item { position: relative; margin-right: 10px; }

.nav__item:after { content: ''; position: absolute; left: -4px; top: 50%; transform: translateY(-1px); height: 2px; background: #171717; width: 12px; opacity: 0; transition: all .3s ease; transform: translateX(-10px); }

.nav__item.active:after, .nav__item:hover:after { opacity: 1; transform: translateX(0); }

.nav__link { display: flex; height: 40px; text-decoration: none; align-items: center; padding: 0 10px; color: #171717; }

@media only screen and (max-width: 1024px) { .nav { font-size: 12px; } }

@media only screen and (max-width: 890px) { .nav { font-size: 16px; } }

.scls { padding: 0; margin: 0; list-style: none; display: flex; }

.scls__item { margin: 0 0 0 10px; }

.scls__item:first-child { margin: 0; }

.scls__link { position: relative; display: flex; height: 40px; width: 40px; align-items: center; justify-content: center; }

.scls__link:hover .scls__img { fill: #24fc89; }

.scls__link:hover:after { opacity: 1; }

.scls__img { width: 17px; height: 14px; width: 100%; fill: #171717; transition: all 0.2s ease; }

.scls__img.sclsl { width: 15px; height: 16px; }

.scls__img.scltw { width: 15px; height: 12px; }

.scls__img.sclyt { width: 17px; height: 12px; }

.scls__img.sclfb { width: 6px; height: 12px; }

.scls__img.sclmed { width: 16px; height: 12px; }

.scls__img.sclgh { width: 15px; height: 14px; }

.menu-toggle { position: fixed; top: 10px; right: 16px; width: 40px; height: 40px; display: none; cursor: pointer; background: none; justify-content: center; padding: 0; border: none; outline: none; z-index: 11; }

.menu-toggle span { display: block; width: 24px; height: 2px; background: #171717; margin: 10px auto; transition: all ease 0.2s; }

.menu-toggle.open span:nth-child(1) { transform: translateY(6px) rotate(45deg); }

.menu-toggle.open span:nth-child(2) { transform: translateY(-6px) rotate(-45deg); }

@media only screen and (max-width: 890px) { .menu-toggle { display: block; } }

/* Content
-----------------------------------------------------------------------------*/
.section__head { display: flex; }

.section__head .section__head-l { max-width: 50%; width: 100%; padding: 0 0 0 100px; }

.section__head .section__head-l .section__title { padding: 80px 0 0 0; }

.section__head .section__head-l.w-100 { max-width: none; }

.section__head .section__head-r { margin: 96px 0 0; max-width: 50%; width: 100%; padding: 0 0 8px 10px; max-width: 480px; }

.section__head .section__head-r.no-width { max-width: 50%; }

.section__head .section__head-r .slider-wrap { margin-right: -126px; }

@media only screen and (max-width: 1480px) { .section__head .section__head-r .slider-wrap { margin-right: 0; } }

.section__title { position: relative; font-size: 52px; line-height: 1.22; font-weight: 700; margin: 0; padding: 76px 0 0 0; }

.section__title .initial { position: relative; top: -8px; width: 0; font-family: "RumbleBrave", Arial, Helvetica, sans-serif; font-size: 150px; line-height: 24px; font-weight: 400; margin: 0 0 0 0; color: #24fc89; }

.section__title .initial span { display: inline-block; position: absolute; right: 100%; }

.section__title .initial.w { left: 10px; }

.section__title.with-dots:before { content: ''; position: absolute; left: -170px; top: -90px; width: 505px; height: 359px; z-index: -1; background: url(../img/title-drops.png) no-repeat; pointer-events: none; }

.section__desc { font-size: 16px; line-height: 1.5; margin: 0; }

.section__desc p { margin: 16px 0 0; }

.section.black { background: #171717; }

.section.black .section__head { color: #fff; }

.section.about .container { position: relative; display: flex; padding: 130px 25px; z-index: 1; justify-content: space-between; }

@media only screen and (max-width: 890px) { .section.about .container { flex-direction: column; padding: 60px 25px; } }

.section.speakers .container { padding-bottom: 0; }

@media only screen and (max-width: 890px) { .section.speakers .container { padding: 60px 20px 0; } }

@media only screen and (max-width: 1024px) { .section__title { font-size: 46px; }
  .section__title .initial { font-size: 140px; } }

@media only screen and (max-width: 890px) { .section__head { flex-direction: column; }
  .section__head .section__head-l { max-width: none; padding: 0 0 0 60px; }
  .section__head .section__head-r { margin: 10px 0 0; max-width: none; padding-left: 60px; }
  .section__head .section__head-r.no-width { max-width: none; }
  .section__head .section__head-r.no-width .loc-slider-nav:after { display: none; }
  .section__title { font-size: 38px; }
  .section__title .initial { font-size: 128px; } }

@media only screen and (max-width: 768px) { .section__desc { font-size: 14px; } }

@media only screen and (max-width: 480px) { .section__title { font-size: 32px; padding: 12px 0 0 72px; }
  .section__title .initial { font-size: 102px; position: relative; right: 0; top: -5px; }
  .section__head .section__head-l, .section__head .section__head-r { padding: 0; }
  .section__head .section__head-l .section__title, .section__head .section__head-r .section__title { padding: 40px 0 0 72px; } }

/* Footer
-----------------------------------------------------------------------------*/
.footer { position: relative; height: 105px; background: #171717; }

.footer .container { padding: 0 254px 0 20px; display: flex; align-items: center; height: 100%; justify-content: space-between; }

.footer .nav { margin: 0 20px; }

.footer .nav__link { color: #24fc89; }

.footer .nav__item:after { background: #24fc89; transform: translateY(-1px); }

@media only screen and (max-width: 890px) { .footer .nav { font-size: 14px; } }

@media only screen and (max-width: 1024px) { .footer { height: auto; }
  .footer .container { padding: 15px 220px 15px 20px; flex-direction: column; align-items: flex-start; }
  .footer .nav { margin: 0 0 0 -20px; } }

@media only screen and (max-width: 768px) { .footer .container { padding: 60px 20px 15px 20px; align-items: center; }
  .footer .nav { display: flex; flex-direction: column; text-align: center; justify-content: center; align-items: center; }
  .footer .nav__item { margin: 0; } }

.f-logo { display: flex; }

.f-logo a { text-decoration: none; }

.f-logo:before { content: ''; display: inline-block; width: 40px; height: 50px; margin: 0 10px 0 0; background: url(../img/f-logo.svg) center center no-repeat; }

.f-logo__desc { margin: auto 0; }

.f-logo__by { font-size: 13px; color: #fff; margin: 6px 0 0; line-height: 1.2; }

.f-logo__org { position: relative; font-size: 25px; font-weight: 700; color: #24fc89; margin: 0; left: -5px; line-height: 1.2; }

@media only screen and (max-width: 1024px) { .f-logo { margin: 10px 0; } }

.f-reserve { position: absolute; right: 0; top: 0; bottom: 0; height: auto; font-size: 14px; }

@media only screen and (max-width: 768px) { .f-reserve { display: flex; order: 1; position: absolute; left: 50%; transform: translateX(-50%); width: 100%; max-width: 320px; top: -20px; height: 60px; border: 3px solid #171717; } }

@media only screen and (max-width: 480px) { .f-reserve { max-width: none; } }

.intro { position: relative; min-height: 100vh; background: url(../img/intro_bg.png) center center no-repeat; background-size: cover; }

