.intro__octo-text { position: absolute; right: 118px; top: 89px; line-height: 1.54; font-size: 14px; text-align: center; font-weight: 600; height: 63px; width: 140px; display: flex; align-items: center; justify-content: center; }

.intro__content { position: relative; display: flex; flex-direction: column; margin: auto 0; width: 100%; }

.intro__desc { max-width: 460px; text-align: center; margin: 22px auto; line-height: 1.72; font-size: 14px; font-weight: 400; }

.intro__reserve { position: absolute; margin: 0; z-index: 1; right: 10px; bottom: 35px; }

.intro__important-guys { position: absolute; left: 0; bottom: 37px; width: 118px; height: 55px; background: url(../img/focus-reactive.png) center center no-repeat; background-size: 100%; display: block; z-index: 1; }

@media only screen and (max-width: 1024px) { .intro__reserve { margin: 112px 0 0; }
  .intro__octo { margin: 0 0 0 -950px; } }

@media only screen and (max-width: 768px) { .intro__desc { font-size: 13px; line-height: 1.4; margin: 9px auto; } }

@media only screen and (max-width: 890px) { .intro__reserve { margin: auto 0 0 0; bottom: 4px; }
  .intro__important-guys { bottom: 4px; left: 10px; } }

@media only screen and (max-width: 768px) { .intro__octo { margin: 290px 0 0 -620px; } }

@media only screen and (max-width: 480px) { .intro__octo { width: 546px; height: 426px; margin: 154px 0 0 -422px; }
  .intro__octo-text { font-size: 10px; right: 57px; top: 62px; }
  .intro__content { position: initial; margin: 0; }
